p.darker {
  background-color: #3a3a3ac1 !important;
}

.text-burning {
  .slide-pics {
    height: 600px;
    left: 100px;
    opacity: 0.8;
    &.child-pic {
      right: 100px;
      left: auto;
      top: 0px;
      img {
        height: 600px;
        left: auto;
        right: 0px;
      }
    }
  }
  img {
    height: 100%;
  }
}

.splide__pagination__page {
  background: #6a0000;
  border: 1px solid rgb(255, 135, 135);
}

.splide__pagination__page.is-active {
  background: rgb(255, 121, 121);
}

.splide__arrow {
  position: absolute;
  z-index: 1;
  bottom: 0px;
  width: 50px;
  height: 50px;
  border-radius: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 0;
  opacity: 1;
  background: none;
  color: white;
  font-size: 20px;
}

.language-toggle {
  z-index: 10000;
  p {
    text-align: center;
    font-weight: bold;
    color: white;
    font-size: 1.2rem;
  }
  span {
    cursor: pointer;
  }
  width: 150px;
  margin: 50px auto;
  font-family: "Open Sans", sans-serif;
}
.text-tweets {
  background-image: url("static/pics/tweets.png");
  background-size: cover;
  .tweets {
    text-align: center;
    position: relative;
    top: 40px;
    color: black;

    .tweet {
      padding: 10px;
      display: inline-block;
      vertical-align: top;
    }
    .slide-heading {
      font-weight: bold;
      font-size: 1.8rem;
      text-shadow: 1px 1px 3px white, 1px -1px 3px white, -1px 1px 3px white;
    }
    .hashtags {
      font-weight: bold;
      text-shadow: 1px 1px 3px white, 1px -1px 3px white, -1px 1px 3px white;
    }
  }
}

.news {
  background-image: url("static/pics/news.png");
  background-size: cover;
  background-position: center;
  height: 100vh;

  position: relative;
  div {
    position: absolute;
    width: 100%;
    height: 100%;
    text-align: center;

    p {
      position: absolute;
      font-size: 10rem;
      font-weight: bold;
      // width: 100%;
      // height: 100%;
      margin: 0px;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      color: white;
      text-shadow: 1px 1px 10px black;
      font-family: "PT Serif", serif;
    }
  }
  // img {
  //   display: inline-block;
  //   max-width: 300px;
  // }
}

.hide-on-desktop {
  display: block;
}

.hide-on-mobile {
  display: none;
}

@media (max-width: 500px) {
  .top {
    .headline {
      font-size: 3rem;
    }
    .subhed {
      max-width: 250px;
      font-size: 1.2rem;
    }
  }
  .slide-pics {
    width: 100% !important;
    position: relative;
  }
  .slide-pics {
    right: 0;
    left: 0;

    img {
      width: 95%;
      top: 10%;
      left: 2.5%;
      max-height: 100%;
      // right: 0px;
      // left: 0px;
    }
  }
  .text-house .wandhama-house {
    margin-left: 0px;
  }
  .text-burning .slide-pics {
    left: 0px;
    opacity: 0.8;
    &.child-pic {
      right: 0px;
      img {
        max-width: 90%;
        height: auto;
      }
      top: 0px;
    }
  }
  .text-victims {
    img {
      height: auto;
      width: 95%;
      top: 10%;
    }
  }
  .news {
    background-image: url("static/pics/news-mobile.png");
    div {
      p {
        font-size: 6rem;
      }
    }
  }

  .hide-on-desktop {
    display: none;
  }

  .hide-on-mobile {
    display: block;
  }

  .text-tweets {
    .tweets {
      top: 40px;
      .tweet {
        width: 80%;
        height: 100%;
        padding: 0px;
      }
    }
  }

  .photos {
    .photo-row {
      width: 100%;
      .photo-container {
        display: inline-block;
        width: 80px;
        height: 80px;

        img {
          clip-path: circle(50px at center);
          width: 60px;
          margin: 15px;
          pointer-events: none;
          &:hover {
            width: 150px;
            margin: -25px;
            clip-path: circle(80px at center);
            z-index: 100;
          }
        }
      }
    }
  }
}

@media (max-width: 769px) {
  .photos {
    .photo-row {
      width: 100%;
      margin-top: 120px;
    }
  }
}
